.box__text {
  width: 95%;
  max-width: 1298px;
  margin: -5px auto 10px;
  padding: 13px 10px;

  font-family: "Caveat", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-size: 22px;

  color: #0f0fa7;
}

.item_text_1 {
  background-repeat: no-repeat;
  background-size: cover;

  margin: 0 auto 10px;

  width: 100%;
  height: 100%;
  max-width: 555px;
  min-height: 250px;

  transform: rotate(-5deg);
}

.text_1 {
  margin: 40px 0 45px;
  padding: 30px 120px 0 90px;
}

.item_text_2 {
  background-repeat: no-repeat;
  background-size: cover;

  margin: -141px auto 10px;

  width: 100%;
  height: 100%;
  max-width: 428px;
  min-height: 270px;
}

.text_2 {
  margin: 0px 0 0px;
  padding: 120px 20px 0 22px;
}

.item_text_3 {
  background-repeat: no-repeat;
  background-size: cover;

  margin: -50px auto 10px;

  width: 100%;
  height: 100%;
  max-width: 312.5px;
  min-height: 600.5px;
}

.text_3 {
  margin: 0px 0 0px;
  padding: 110px 20px 0 30px;
}

.item_text_4 {
  background-repeat: no-repeat;
  background-size: cover;

  margin: -380px auto 10px;
  width: 100%;
  height: 100%;
  max-width: 343px;
  min-height: 202px;

  transform: rotate(-3deg);
}

.text_4 {
  margin: 0px 0 0px;
  padding: 80px 14px 0 18px;
}

.item_text_5 {
  background-repeat: no-repeat;
  background-size: cover;

  margin: -85px auto 40px;
  width: 100%;
  height: 100%;
  max-width: 555px;
  min-height: 250px;

  transform: rotate(-1deg);
}

.text_5 {
  margin: 0px 0 0px;
  padding: 46px 94px 0 85px;
}

