.span_dekor {
  display: block;
  width: 100%;
  color: #739ca5;
  text-align: center;
}

span {
  color: red;
}

.span_dekor_top,
.span_dekor_bot {
  display: block;

  margin: -5px auto;
  color: #739ca5;
  text-align: center;

  pointer-events: none;
}

