.canvas {
  width: 47%;
  /* width: 71%; */
  min-width: 450px;
  height: 100%;

  margin: 15px;

  border-radius: 20px;
  border: 1px solid pink;
}

.canvas:hover {
  transform: scale(1.2);
  transition: all 0.3s;
}

